            </ul>
        </div>
         <div class="column">
            <h3 class="subHeader">Shop By Sport</h3>
            <ul>
                <li>Running</li>
                <li>Basketball</li>
                <li>Football</li>
                <li>Tennis</li>
                <li>Yoga</li>
                <li>SkateBoarding</li>
                <li>Trekking</li>
                <li>Golf</li>
                <li>Gym and training</li>
                <li>Cricket</li>
            </ul>
        </div>
         <div class="column">
            <h3 class="subHeader">Shop by Brand</h3>
            <ul>
                <li>Adidas</li>
                <li>Nike</li>
                <li>Puma</li>
                <li>Reebok</li>
                <li>Under Armour</li>
