                <li>Gucci</li>

            </ul>
        </div>
    </div>
    <div :class="[initial?'oerlay':boxContent?'overlayActive':'overlayClose']" @mouseover="closeOverlay"></div>
  </div>
</template>
    </div>
    
</template>

<script>
export default {
        data: () => ({
      overlay: false,
      initial:true,
      boxContent:false,
      prevHolder:1,
      currHolder:1,
    }),
    methods: {
      toggleOverlay(holder) {
          this.currHolder = holder;

          if(this.currHolder==this.prevHolder)
          {
              this.initial=false;
        this.boxContent = true;
        this.overlay = true;
        this.prevHolder=this.currHolder;
          }
          else
          {
              this.prevHolder=this.currHolder;
          }
      },
      closeOverlay(){
          this.initial=false;
        this.boxContent = false;
        this.overlay = false;
      }
    },

    watch: {
      overlay (val) {
        val && setTimeout(() => {
          this.overlay = false
        }, 2000)
      },
    },

}
</script>

<style scoped>
#papa{
    display:grid;
    grid-template-columns: repeat(3,100px);
    padding-left:37%;
}
#controller h2{
    font-weight:500;
    font-style:normal;
    font-size:19px;
    cursor:pointer;
    width:fit-content;
   
}
#container h2:hover{
    font-weight: 600;
}
.expansionBody{
    display:none;
    margin-top:10px;
    width:100%;
    height:0px;
    background-color:rgb(240, 240, 240);
}
.expand{
    margin-top:10px;
    width:100%;
    height:300px;
    background-color:white;
    animation-name:animateExpansion;
    animation-duration:0.25s;
    animation-fill-mode: forwards;
}
.collapse{
    margin-top:10px;
    width:100%;
    height:400px;
    background-color:white;
    animation-name:animateCollapse;
    animation-duration:0.25s;
    animation-fill-mode: forwards;
}
@keyframes animateExpansion{
    from{
        display:none;
        height: 0px;
    }
    to{
        display:block;
        height:500px;
    }
}
@keyframes animateCollapse{
    from{
        height:500px;
    }
    to{
        height:0px;
    }
}
.overlay{
    cursor: pointer;
    height:0px;
    top: 0;
  position: absolute;
  left: 0;
  width: 100%;
  z-index: 10;
  background-color: rgba(0,0,0,0.5);
}
.overlayActive{
    cursor: pointer;
  position: absolute;
  left: 0;
  width: 100%;
  z-index: 10;
  background-color: rgba(0,0,0,0.5);
  animation-name: overlayExpand;
  animation-duration: 0.25s;
  backdrop-filter: blur(1px);
    animation-fill-mode: forwards;
}
.overlayClose{
    cursor: pointer;
  position: absolute;
width: 100%;
  left: 0;
  z-index: 10;
  background-color: rgba(0,0,0,0.5);
    animation-name: overlayCollapse;
    animation-duration: 0.25s;
    animation-fill-mode: forwards;
}
/*animation for overlays*/
@keyframes overlayExpand{
    from{
        height:0px;
    }
    to{
        height:100%;
    }
}
@keyframes overlayCollapse{
    from{
        height:100%;
    }
    to{
        height:0px;
    }
}
#expansionBody{
    display: grid;
    grid-template-columns:repeat(4,auto);
    padding-left:13%;
     padding-right:13%;
